.contest-checkbox {
  vertical-align: middle;
  position: absolute;
  opacity: 0;
  visibility: hidden; }
  .contest-checkbox + label {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    cursor: pointer;
    padding-right: 42px; }
  .contest-checkbox:not(checked) + label {
    position: relative; }
    .contest-checkbox:not(checked) + label::before, .contest-checkbox:not(checked) + label::after {
      content: "";
      display: block;
      position: absolute;
      border-radius: 2px;
      top: 50%;
      transform: translateY(-50%); }
    .contest-checkbox:not(checked) + label::before {
      width: 20px;
      height: 20px;
      border: 1px solid #e6e6e6;
      right: 0;
      background: #e6e6e6; }
    .contest-checkbox:not(checked) + label::after {
      font-family: 'FontAwesome';
      content: "\f14a";
      width: 20px;
      height: 20px;
      opacity: 0;
      right: 0;
      color: #01B8A4;
      font-size: 23px;
      vertical-align: middle;
      line-height: 0.9;
      background: #fff; }
  .contest-checkbox:checked + label:after {
    opacity: 1; }

.contest-radio {
