    .contest-radio:not(checked) + label::before, .contest-radio:not(checked) + label::after {
      content: "";
      display: block;
      position: absolute;
      border-radius: 50%;
      top: 50%;
      transform: translateY(-50%); }
    .contest-radio:not(checked) + label::before {
